.notFound-container * {
    transition: all 0.6s;
}

.notFound-container html {
    height: 100%;
}

.notFound-container body {
    font-family: 'Lato', sans-serif;
    color: #888;
    margin: 0;
}

.notFound-container {
    display: table;
    width: 100%;
    height: 100vh;
    text-align: center;
}

.fof {
    display: table-cell;
    vertical-align: middle;
}

.fof h1 {
    font-size: 50px;
    display: inline-block;
    padding-right: 12px;
    animation: type 0.5s alternate infinite;
}

@keyframes type {
    from {
        box-shadow: inset -3px 0px 0px #888;
    }
    to {
        box-shadow: inset -3px 0px 0px transparent;
    }
}
